import React, {Component} from 'react';
import Header from './Header';
import MessageInput from "./MessageInput";
import ChatFeed from "./ChatFeed";
import {Provider} from 'react-redux';
import {createStore, combineReducers, applyMiddleware} from 'redux';
import chatBusReducers from './../reducers/reducers';

/**
 * Root component of the application.
 */
class ChatBus extends Component {

  render() {
    const chatBusReducers = combineReducers({
      chatBusState: chatBusReducers,
    });

    /* eslint-disable no-underscore-dangle */
    const store = createStore(
      chatBusReducers,
      window.__REDUX_DEVTOOLS_EXTENSION__ && window.__REDUX_DEVTOOLS_EXTENSION__()
    );
    /* eslint-enable */

    return (
      <Provider store={store}>
        <ChatApplication/>
      </Provider>
    );
  }
}

function ChatApplication(props) {
  return (<div className="chatBus-application">
    <Header/>
    <ChatFeed/>
    <MessageInput/>
  </div>);
}

export default ChatBus;



